.App{
 
  width: 60%;
  margin: 5% auto;
  padding: 2%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-radius: 4px;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(50, 50, 50, 0.75);
  -moz-box-shadow:    0px 0px 10px 0px rgba(50, 50, 50, 0.75);
  box-shadow:         0px 0px 10px 0px rgba(50, 50, 50, 0.75);
}
@media screen and (max-width: 490px){
  .App{
    width: 90%;
  }
 
}
.App-header{
  width: 400px;
  padding: 0% 2%;
}

.container{
  width: 50%;
  float:right;
  
}
@media screen and (max-width: 1449px){
  .App-header{
    width: 100%;
  }
  .container{
    width: 100%;
  }

}
.quest{
  padding: 2%;
  margin-bottom: 3%;
  display: flex;
  justify-items: center;
  justify-content: space-between;
  -webkit-box-shadow: 0px 0px 6px 0px rgba(50, 50, 50, 0.75);
  -moz-box-shadow:    0px 0px 6px 0px rgba(50, 50, 50, 0.75);
  box-shadow:         0px 0px 6px 0px rgba(50, 50, 50, 0.75);
}
button{
  width: 30px;
  height: 30px;
  margin-top: 3%;
  border-radius: 100%;
  border:none;
background: none;
  cursor: pointer;
}
.icon{
  width: 20px;
  height: 20px;
  border-radius: 100%;
  background: rgb(216, 207, 207);
  padding: 20%;
  color: rgb(90, 88, 88);
}

.text{
  text-align: center;
  color: gray;
  font-size: 14px;
}